
  .container {
    padding: 50px 30px;
    text-align: center;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    background-color: rgb(219, 240, 245);
    min-height: 85vh;
  }

  .left{
    padding-bottom: 20px;
  }
  
  .heading {
    margin: 0 auto;
    max-width: 500px;
    color: #000;
    font-size: 3.1rem;
    /* line-height: 4.1rem; */
    font-weight: bold;
    letter-spacing: 1px;
    font-family: cursive;
    font-size: 30px;
  }
  
  .heading span {
    background-clip: text;
    background-image: linear-gradient(to right, #239ce2, #1369b9);
    color: transparent;
    font-family: cursive;
    font-size: 30px;
  }
  
  .right img {
    width: 400px;
  }
  
  /* Media Queries for Responsive Layout */
  
  @media (max-width: 400px) {
    .container {
      padding: 30px;
      min-height: auto;
    }
  
    .heading {
      font-size: 2.2rem;
      line-height: 3.2rem;
    }
  
    .right img {
      width: 300px;
    }
  }
  