

  .container {
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    padding-top: 0;
  }
  
  .heading {
    font-weight: 700;
    font-size: 1.6rem;
    
  }
  
  .toolbar {
    width: 100%;
    display: flex;
    gap: 40px;
    justify-content: space-between;
    align-items: center;
  }
  
  .colors {
    display: flex;
    gap: 12px;
    padding: 0 30px;
  }
  
  .colors .color {
    height: 36px;
    width: 36px;
    border-radius: 50%;
    background-color: #239ce2;
  }
  
  .colors .active {
    border: 2px solid #000;
  }
  
  .toolbar button {
    padding: 8px 16px;
    border-radius: 5px;
    background-color: #4b545a;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 500;
    font-size: 1rem;
    letter-spacing: 1px;
    display: flex;
    gap: 5px;
    align-items: center;
    cursor: pointer;
  }

  .toolbar button:hover{
    background-color: rgb(96, 80, 57);
  }
  
  .toolbar button svg {
    height: 20px;
    width: 20px;
  }
  
  .main {
    display: flex;
    flex-direction: column;
    gap: 30px;
    width: 100%;
  }
  
  /* Media Queries for Responsive Layout */
  
  @media (max-width: 600px) {
    .toolbar {
      flex-wrap: wrap;
      justify-content: center;
      gap: 20px;
    }
  
    .colors {
      padding: 0;
      justify-content: center;
    }
  
    .toolbar button {
      font-size: 0.875rem;
    }
  }
  