

  .container {
    --color: #239ce2;
    min-width: 250px; /* Updated minimum width */
    max-width: 850px;
    margin: 0 auto;
    flex: 1.2;
    height: fit-content;
    min-height: 900px;
    box-shadow: 1px 1px 3px 2px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding: 30px;
  
  }

 
  
  .header {
    display: flex;
    flex-direction: column;
    /* flex-wrap: wrap; */
    
  }
   

  

  .header .heading {
    font-size: 2.7rem;
    font-weight: 500;
    text-transform: capitalize;
    overflow-wrap: break-word;
    word-break: break-word;
  }
 
  

  .header .subHeading {
    color: var(--color);
    font-weight: 500;
    padding-top: 7px;
    font-size: 1.1rem;
    overflow-wrap: break-word;
    word-break: break-word;
  }
  
  .header .links {
    margin-top: 15px;
    display: flex;
    gap: 30px;
    flex-wrap: wrap;
    row-gap: 10px;
    font-family:  sans-serif;
    
  }
  
  .header .link {
    font-size: 0.875rem;
    display: flex;
    align-items: center;
    gap: 5px;
    cursor: pointer;
    overflow-wrap: break-word; /* Added property to wrap long words */
    word-break: break-word; /* Added property to break long words */
    
    
    
  }
  
  .header .link svg {
    color: var(--color);
    height: 16px;
    width: 16px;
  }
  
  .main {
    display: flex;
    flex-wrap: wrap; /* Added flex-wrap for responsive layout */
    gap: 30px;
  }
  
  .col1 {
    flex: 1; /* Adjusted flex value */
    display: flex;
    flex-direction: column;
    gap: 20px;
    overflow-wrap: break-word; /* Added property to wrap long words */
  word-break: break-word; /* Added property to break long words */
  max-width: 100%; /* Added property to limit width of content */
    
  }
  
  .col2 {
    flex: 1; /* Adjusted flex value */
    display: flex;
    flex-direction: column;
    gap: 20px;
    overflow-wrap: break-word; /* Added property to wrap long words */
  word-break: break-word; /* Added property to break long words */
  max-width: 100%; /* Added property to limit width of content */
   
  }

  @media (max-width: 500px) {
    .col1,
    .col2 {
      flex: 100%; /* Change flex value to occupy full width */
      gap: 10px; /* Decrease the gap between items */
    }
  }
  
  .section .sectionTitle {
    font-size: 1.4rem;
    font-weight: bold;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    width: 100%;
    border-bottom: 2px solid #8d8a8a;
  }
  
  

  .section .content {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 15px 0;
    
  }


 
  
  .section .content .item {
    border-bottom: 1px dotted lightgray;
    display: flex;
    flex-direction: column;
    gap: 4px;
    padding-bottom: 10px;
  }
  
  .section .content .item:last-child {
    border-bottom: none;
  }
  
  .content .title {
    font-weight: 600;
    font-size: 1rem;
    font-family: Arial, Helvetica, sans-serif;
    line-height: 1.3rem;
    padding-left: 7px;
  }

  
  .content .subTitle {
    font-weight: bold;
    color: var(--color);
    font-size: 1rem;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }
  
  .content .overview {
    font-size: 0.875rem;
    text-align: justify;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: #383535;
  }

  li{
    color: #383535;
    font-family:  sans-serif;

    
  }
  
  .content .link {
    display: flex;
    gap: 5px;
    font-size: 0.65rem;
    cursor: pointer;
    color: var(--color);
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  }
  
  .content .link svg {
    height: 14px;
    width: 14px;
    color: var(--color);
  }
  
  .content .date {
    display: flex;
    gap: 5px;
    align-items: center;
    font-size: 0.875rem;
  }
  
  .content .date svg {
    height: 14px;
    width: 14px;
  }
  
  .content .points,
  .numbered {
    padding-left: 18px;
    font-size: 0.875rem;
    text-align: justify;
    

  }
  
  .numbered li {
    list-style-type: decimal;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: #0f0e0e;
    font-family:  sans-serif;
  }
  
  .hidden {
    display: none;
  }

  


  .container {
    @media (max-width: 750px) {
      padding: 15px;
      gap: 10px;
    }
  }
  
  .main {
    @media (max-width: 750px) {
      flex-direction: column;
    }


  }
  
  .col1 {
    @media (max-width: 750px) {
      flex: 1;
      gap: 10px;

    }
  }
  
  .col2 {
    
    @media (max-width: 750px) {
      flex: 1;
      gap: 10px;
    }
  }


  
