.container {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }
  
  label {
    font-weight:bold;
    font-size: 1rem;
    margin-bottom: 3px;
    margin-top: 27px;
    font-style: inherit;
  }
  
  input {
    border: 1px solid #876d6d;
    outline: none;
    padding: 10px 12px;
    font-size: 1rem;
    border-radius: 5px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }
  
  input:hover {
    border: 1px solid #e6dede;
    
  }
  
  input:focus {
    border: 1px solid #239ce2;
  }