
  .container {
    min-width: 250px;
    max-width: 950px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
    padding: 10px;
    height: fit-content;
    margin: 0 auto;
    background-color:  rgb(251, 253, 254);
  }
  
  @media (min-width: 500px) {
    .container {
      padding: 30px;
    }
  }
  
  .header {
    display: flex;
    flex-wrap:wrap;
    
    gap: 5px;
    overflow-x: auto;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }

  @media (max-width: 391px){
    .header .section{
      font-size: 10px;
    }
  }

  

  .header::-webkit-scrollbar {
    height: 7px;
  }
  
  .header::-webkit-scrollbar-thumb {
    border-radius: 20px;
    background-color: #c7c7c7;
  }
  
  .header .section {
    padding: 10px;
    border-bottom: 2px solid transparent;
    font-weight: 500;
    font-size: 1rem;
    white-space: nowrap;
    cursor: pointer;
  }
  
  .body {
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .chips {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px;
  }
  
  .chip {
    display: flex;
    gap: 5px;
    align-items: center;
    padding: 4px 8px;
    background-color: #808080;
    border-radius: 20px;
    cursor: default;
  }
  
  .chip p {
    font-weight: 500;
    line-height: 1.25rem;
    color: #fff;
  }
  
  .chip svg {
    color: #fff;
    height: 18px;
    width: 18px;
    cursor: pointer;
  }
  
  .chips .new {
    color: #239ce2;
    font-weight: bold;
    letter-spacing: 1px;
    cursor: pointer;
  }
  
  .chips .active {
    background-color: #239ce2;
  }
  
  .body button {
    /* width: fit-content; */
    width: 60%;
    padding: 8px 16px;
    border-radius: 5px;
    background-color: #7b95a2;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 500;
    font-size: 1rem;
    letter-spacing: 1px;
    display: flex;
    gap: 5px;
    /* align-items: center; */
   justify-content: center;
    margin: auto;
    cursor: pointer;
    transition: 200ms;
  }

  .body button:hover{
    background-color: rgb(112, 105, 97);
  }
  
  @media (max-width: 350px) {
    .body button {
      font-size: 0.9rem;
    }
  }
  